flex-direction: column;\n  width: 100%;\n\n  animation: 0.4s form-in;\n  animation-fill-mode: both;\n  animation-timing-function: ease;\n\n  label {\n    font-size: var(--font-body);\n    font-weight: var(--font-weight-bold);\n    margin-top: var(--spacing-m);\n  }\n}\n\n.sr-picker {\n  display: flex;\n  justify-content: space-between;\n  margin-bottom: 25px;\n}\n.sr-picker button {\n  background: none !important;\n  border: none;\n  padding: 0 !important;\n  /*optional*/\n  font-family: arial, sans-serif;\n  /*input has OS specific font-family*/\n  color: var(--accent-color);\n  cursor: pointer;\n  width: 75px;\n  box-shadow: none;\n  border-radius: 0;\n}\n.sr-picker button:hover,\n.sr-picker button:focus,\n.sr-picker button.selected {\n  outline: none;\n  box-shadow: none;\n  border-radius: 0;\n}\n.sr-picker button:hover,\n.sr-picker button:focus {\n  border-bottom: 2px solid var(--secondary-color);\n}\n.sr-picker button.selected {\n  border-bottom: 2px solid var(--accent-color);\n}\n\n/* Inputs */\n.stripe-card__form-input {\n  border: 1px solid var(--gray-border) !important;\n  border-radius: var(--border-radius);\n  padding: var(--spacing-m) var(--spacing-s);\n  transition: box-shadow 0.2s ease;\n  background: white !important;\n  -moz-appearance: none;\n  -webkit-appearance: none;\n  appearance: none;\n  width: 284px;\n  color: var(--color-black) !important;\n\n  &:focus {\n    box-shadow: 0 0 0 1px rgba(50, 151, 211, 0.3), 0 1px 1px 0 rgba(0, 0, 0, 0.07), 0 0 0 4px rgba(50, 151, 211, 0.3) !important;\n    outline: none;\n    z-index: 9;\n  }\n\n  &::placeholder {\n    color: var(--gray-light);\n  }\n\n  &.disabled {\n    opacity: 0.4;\n    padding: 0;\n    height: var(--height-input);\n  }\n}\n\n.sr-result {\n  height: 44px;\n  -webkit-transition: height 1s ease;\n  -moz-transition: height 1s ease;\n  -o-transition: height 1s ease;\n  transition: height 1s ease;\n  color: var(--font-color);\n  overflow: auto;\n}\n.sr-result code {\n  overflow: scroll;\n}\n.sr-result.expand {\n  height: 350px;\n}\n\n.sr-combo-inputs-row {\n  box-shadow: 0px 0px 0px 0.5px rgba(50, 50, 93, 0.1), 0px 2px 5px 0px rgba(50, 50, 93, 0.1),\n    0px 1px 1.5px 0px rgba(0, 0, 0, 0.07);\n  border-radius: 7px;\n}\n\n/* Buttons and links */\n.button--card-link {\n  background-color: rgb(30, 166, 114);\n  border-radius: 6px;\n  color: white;\n  border: 0;\n  padding: 12px 16px;\n  margin-top: 16px;\n  font-weight: 600;\n  cursor: pointer;\n  transition: all 0.2s ease;\n  display: block;\n  box-shadow: 0px 4px 5.5px 0px rgba(0, 0, 0, 0.07);\n  width: 100%;\n  max-width: 280px;\n\n  .button__content {\n    justify-content: center;\n  }\n\n  &:hover {\n    filter: contrast(115%);\n  }\n\n  &:active {\n    transform: translateY(0px) scale(0.98);\n    filter: brightness(0.9);\n  }\n\n  &:disabled {\n    opacity: 0.5;\n    cursor: none;\n  }\n}\n\n/* Code block */\ncode,\npre {\n  font-family: 'SF Mono', 'IBM Plex Mono', 'Menlo', monospace;\n  font-size: 12px;\n}\n\n/* Responsiveness */\n@media (max-width: 720px) {\n  .sr-header__logo {\n    background-position: center;\n  }\n\n  .sr-payment-summary {\n    text-align: center;\n  }\n\n  .sr-payment-form {\n    padding: 10px 34px;\n  }\n\n  .sr-content {\n    display: none;\n  }\n}\n\n/* todo: spinner/processing state, errors, animations */\n\n.stripe__spinner,\n.stripe__spinner:before,\n.stripe__spinner:after {\n  border-radius: 50%;\n}\n.stripe__spinner {\n  color: #ffffff;\n  font-size: 22px;\n  text-indent: -99999px;\n  margin: 0px auto;\n  position: relative;\n  width: 20px;\n  height: 20px;\n  box-shadow: inset 0 0 0 2px;\n  -webkit-transform: translateZ(0);\n  -ms-transform: translateZ(0);\n  transform: translateZ(0);\n}\n.stripe__spinner:before,\n.stripe__spinner:after {\n  position: absolute;\n  content: '';\n}\n.stripe__spinner:before {\n  width: 10.4px;\n  height: 20.4px;\n  background: var(--accent-color);\n  border-radius: 20.4px 0 0 20.4px;\n  top: -0.2px;\n  left: -0.2px;\n  -webkit-transform-origin: 10.4px 10.2px;\n  transform-origin: 10.4px 10.2px;\n  -webkit-animation: loading 2s infinite ease 1.5s;\n  animation: loading 2s infinite ease 1.5s;\n}\n.stripe__spinner:after {\n  width: 10.4px;\n  height: 10.2px;\n  background: var(--accent-color);\n  border-radius: 0 10.2px 10.2px 0;\n  top: -0.1px;\n  left: 10.2px;\n  -webkit-transform-origin: 0px 10.2px;\n  transform-origin: 0px 10.2px;\n  -webkit-animation: loading 2s infinite ease;\n  animation: loading 2s infinite ease;\n}\n\n@-webkit-keyframes loading {\n  0% {\n    -webkit-transform: rotate(0deg);\n    transform: rotate(0deg);\n  }\n  100% {\n    -webkit-transform: rotate(360deg);\n    transform: rotate(360deg);\n  }\n}\n@keyframes loading {\n  0% {\n    -webkit-transform: rotate(0deg);\n    transform: rotate(0deg);\n  }\n  100% {\n    -webkit-transform: rotate(360deg);\n    transform: rotate(360deg);\n  }\n}\n\n/* Animated form */\n\n.hidden {\n  display: none;\n}\n\n@keyframes field-in {\n  0% {\n    opacity: 0;\n    transform: translateY(8px) scale(0.95);\n  }\n  100% {\n    opacity: 1;\n    transform: translateY(0px) scale(1);\n  }\n}\n\n@keyframes form-in {\n  0% {\n    opacity: 0;\n    transform: scale(0.98);\n  }\n  100% {\n    opacity: 1;\n    transform: scale(1);\n  }\n}\n\n.sr-payment-form {\n  padding: 10px 35px;\n  padding-bottom: 28px;\n}\n\n.stripe__complete-verification-button {\n  margin-right: 10px !important;\n}\n\n// currency selector\n.currency-to-use__div {\n  margin-bottom: 11px;\n}\n\n.currency-to-use__header {\n  margin-bottom: 5px;\n}\n\n.currency-to-use__selector {\n  max-width: 113px;\n}\n\n.successCard {\n  .button {\n    background-color: var(--color-background);\n  }\n\n  .card__body {\n    display: flex;\n    background-color: var(--color-header-button);\n    padding: var(--spacing-s);\n    border-radius: var(--border-radius);\n\n    .card-wrapper {\n      .jp-card-container {\n        margin: 0;\n        width: 360px;\n        height: 210px;\n      }\n    }\n    .card-meta {\n      margin-left: var(--spacing-m);\n\n      label {\n        display: block;\n        margin-top: var(--spacing-m);\n        font-size: var(--font-size-medium);\n        font-weight: var(--font-weight-bold);\n      }\n\n      select {\n        background-color: var(--color-background);\n        &:hover {\n          background-color: var(--color-primary);\n          color: var(--color-primary-contrast) !important;\n        }\n        &:focus-visible {\n          color: var(--color-text);\n        }\n      }\n    }\n\n    @media (max-width: $breakpoint-small) {\n      display: block;\n      padding: var(--spacing-s) !important;\n\n      .card-wrapper {\n        transform: unset;\n        margin-top: var(--spacing-xxs);\n        margin-bottom: var(--spacing-l);\n        .jp-card-container {\n          margin: 0;\n          width: 100%;\n          height: 210px;\n        }\n      }\n      .card-meta {\n        margin-left: 0;\n\n        label {\n          margin-top: var(--spacing-l);\n        }\n      }\n    }\n  }\n}\n\n.modal {\n  .add-payment-card {\n    padding: 0;\n  }\n  .card__body {\n    .card-meta {\n      margin: 0;\n    }\n  }\n  .card-wrapper {\n    display: none;\n  }\n}\n\n.add-payment-card {\n  .card__header--between,\n  .card__title-section {\n    display: block;\n  }\n}\n\n// center loading div\n.getting-card-status .card__header--between {\n  justify-content: center;\n}\n\n#card-name {\n  width: 284px;\n  box-shadow: unset;\n  color: #32325d;\n  background-color: white !important;\n}\n\n.remove-card__button {\n  margin-left: var(--spacing-s);\n}\n"],"sourceRoot":""}]);
